<template>
	<div class="container" @click="clickHandle('test click', $event)">

		<div class="userinfo" @click="bindViewTap">
			<img class="userinfo-avatar" v-if="userInfo.avatarUrl" :src="userInfo.avatarUrl" background-size="cover" />
			<div class="userinfo-nickname">
				<card :text="userInfo.nickName"></card>
			</div>
		</div>

		<div class="usermotto">
			<div class="user-motto">
				<card :text="motto"></card>
			</div>
		</div>
		<a href="/pages/hotsearch/main" class="counter">热搜榜</a>
		<a href="/pages/counter/main" class="counter">检索页面</a>
	</div>
</template>

<script>
	import card from '@/components/card'

	var Fly = require("flyio/dist/npm/wx")
	var fly = new Fly

	export default {
		data() {
			return {
				motto: '欢迎来到CLW系统',
				userInfo: {},
				te: '123'
			}
		},

		components: {
			card
		},

		methods: {
			bindViewTap() {
				const url = '../logs/main'
				wx.navigateTo({
					url
				})
			},
			getUserInfo() {
				// 调用登录接口
				wx.login({
					success: () => {
						wx.getUserInfo({
							success: (res) => {
								this.userInfo = res.userInfo
							}
						})
					}
				})
			},
			clickHandle(msg, ev) {
				console.log('clickHandle:', msg, ev)
			},
			ssss(){
				this.te = '55555';
			}
/*			testfly() {
				fly.get('http://39.106.221.38:8080/clw-ser/bookList')
				.then((response) => {
				 this.te = 	response.data.BookList[1].name					
				})
			}*/

		},

		created() {
			// 调用应用实例的方法获取全局数据
			this.getUserInfo()
		}
	}
</script>

<style >
	.userinfo {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	
	.userinfo-avatar {
		width: 128rpx;
		height: 128rpx;
		margin: 20rpx;
		border-radius: 50%;
	}
	
	.userinfo-nickname {
		color: #aaa;
	}
	
	.usermotto {
		margin-top: 120px;
	}
	
	.form-control {
		display: block;
		padding: 0 12px;
		margin-bottom: 5px;
		border: 1px solid #ccc;
	}
	
	.counter {
		display: inline-block;
		margin: 10px auto;
		padding: 5px 10px;
		color: blue;
		border: 1px solid blue;
	}
</style>


